  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

@keyframes blinker {
  50% {
    opacity: 0; } }

@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-in-bottom-50 {
  0% {
    opacity: 0;
    transform: translateY(50px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fade-in-bottom-20 {
  0% {
    opacity: 0;
    transform: translateY(20px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fade-in-top-50 {
  0% {
    opacity: 0;
    transform: translateY(-50px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes move-right {
  0% {
    transform: translateX(-100%);
    background-color: #2c39f2; }
  100% {
    transform: translateX(0);
    background-color: #2c39f2; } }

@keyframes move-left {
  0% {
    transform: translateX(0);
    background-color: #2c39f2; }
  100% {
    transform: translateX(-100%);
    background-color: #2c39f2; } }

@keyframes fade-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.animation-group .fib {
  opacity: 0; }

.animation-group .fi {
  opacity: 0; }

.animation-group .blur {
  filter: blur(10px); }

.animation-group .d-100 {
  animation-delay: 0.1s; }

.animation-group .d-200 {
  animation-delay: 0.2s; }

.animation-group .d-300 {
  animation-delay: 0.3s; }

.animation-group .d-400 {
  animation-delay: 0.4s; }

.animation-group .d-500 {
  animation-delay: 0.3s; }

.animation-group .d-600 {
  animation-delay: 0.6s; }

.animation-group .d-700 {
  animation-delay: 0.7s; }

.animation-group .d-800 {
  animation-delay: 0.8s; }

.animation-group .d-900 {
  animation-delay: 0.9s; }

.animation-group .d-1000 {
  animation-delay: 1s; }

.animation-group .d-1100 {
  animation-delay: 1.1s; }

.animation-group .d-1200 {
  animation-delay: 1.2s; }

.animation-group .d-1300 {
  animation-delay: 1.3s; }

.animation-group .d-1400 {
  animation-delay: 1.4s; }

.animation-group .d-1500 {
  animation-delay: 1.5s; }

.animation-group .d-1600 {
  animation-delay: 1.6s; }

.animation-group .d-1700 {
  animation-delay: 1.7s; }

.animation-group.show .fib {
  animation-name: fade-in-bottom-50;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  animation-fill-mode: both; }
  @media (max-width: 767px) {
    .animation-group.show .fib {
      animation-name: fade-in-bottom-20; } }

.animation-group.show .fi {
  animation-name: fade-in;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  animation-fill-mode: both; }

.animation-group.show .fit {
  animation-name: fade-in-top-50;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  animation-fill-mode: both; }

.animation-group.show .move-right {
  animation-name: move-right;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  visibility: visible;
  animation-fill-mode: forwards; }
  .animation-group.show .move-right .intro__col--left {
    display: none; }
  .animation-group.show .move-right .intro__col--right {
    display: block; }

.animation-group.show .move-left {
  animation-name: move-left;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  visibility: visible;
  animation-fill-mode: forwards; }
  .animation-group.show .move-left .intro__col--left {
    display: block;
    transform: translateX(170%); }
  .animation-group.show .move-left .intro__col--right {
    display: block;
    transform: translateX(-100%); }

.animation-group.show .blink {
  animation-name: blinker;
  animation-duration: 0.6s;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-iteration-count: infinite; }

.animation-group.show .blink-ad-2 {
  animation-name: blinker;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-iteration-count: infinite; }

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
  z-index: 1100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffffff; }
  .preloader__img svg {
    width: 800px;
    height: 100px; }
    @media (max-width: 1023px) {
      .preloader__img svg {
        width: 550px;
        height: 70px; } }
  .preloader--hidden {
    display: none; }
  .preloader .preloader__text {
    position: absolute;
    margin: 0;
    font-size: 24px;
    line-height: 22px; }
    .preloader .preloader__text--desktop {
      top: 84vh; }
      @media (max-width: 1023px) {
        .preloader .preloader__text--desktop {
          display: none; } }
    .preloader .preloader__text--tablet {
      top: 88vh;
      display: none; }
      @media (max-width: 1023px) {
        .preloader .preloader__text--tablet {
          display: block; } }

.header {
  display: flex;
  padding: 25px 35px; }
  .header__wrapper {
    width: 100%;
